<template>
    <div>
        <b-form-select v-model="selected" :options="options"></b-form-select>
        <b-form-select v-model="selected" :options="options" size="sm" class="mt-3"></b-form-select>
        <div class="mt-3">
            Selected:
            <strong>{{ selected }}</strong>
        </div>
    </div>
</template>

<script>

import Vue from 'vue'
import api from '@/plugins/api';
import VueGoogleApi from 'vue-google-api'

// 設定 Vue Google Api 
// https://www.npmjs.com/package/vue-google-api
const config = {
  clientId: '454516942573-f1md44klo63oj5gmghu5jvm0ajnkkt92.apps.googleusercontent.com',
  apiKey: 'AIzaSyAZ9Dko8GtcVbsLMWndPCVTaWI0X8KjO-c',
  // spreadSheetId: '1kNNoLQflspHyXrLjdpz5QgGihVzG5WtppsykZAWtWoI',
  scope: 'https://www.googleapis.com/auth/spreadsheets.readonly',
  discoveryDocs: ["https://sheets.googleapis.com/$discovery/rest?version=v4"],
};
// 初始化 Vue Google Api 
Vue.use(VueGoogleApi, config);

export default {
    name: "Main",
    mixins: [api],
    data() {
        return {
            selected: null,
            options: [
                { value: null, text: 'Please select an option' },
                { value: 'a', text: 'This is First option' },
                { value: 'b', text: 'Selected Option' },
                { value: { C: '3PO' }, text: 'This is an option with object value' },
                { value: 'd', text: 'This one is disabled', disabled: true }
            ]
        };
    },
    created()
    {
        // this.$gapi.isSignedIn()
        //     .then(result => {
        //         console.log(result ? 'Signed in' : 'Signed out')
        //     });
        // this.getData();

        EventBus.$on('api_data_is_ready', (val)=>{
            console.log('api_data_is_ready');
            console.log(val);
            this.productData = val.data;
            this.productDataReady = true;
            console.log(this.productData);
        });
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* h1,
h2 {
    font-weight: normal;
}
ul {
    list-style-type: none;
    padding: 0;
}
li {
    display: inline-block;
    margin: 0 10px;
}
a {
    color: #42b983;
} */
</style>



